import Vue from 'vue'
import {isMobile, accessToken, spToFt, lightenDarkenColor, assignObj, clone ,oem , isPublish , serverUrl} from "./comm";
import {systemConfig} from "../config/system";
import {langConfig} from "../config/lang";
import {apiConfig} from "../config/api";

/**
 * 
 * @param key api.settings
 * @param where 
 * @return {*}
 */
Vue.prototype.$C = function (key, where = 'sys') {
    //
    let settings = {};
    switch (where) {
        case 'sys':
            Object.assign(settings, systemConfig);
            let serverSystemConfig = this.$store.state.serverSystemConfig;
            settings = assignObj(settings, serverSystemConfig);
            break;
        case 'lang':
            Object.assign(settings, langConfig);
            let serverLangConfig = this.$store.state.serverLangConfig;
            settings = assignObj(settings, serverLangConfig);
            break;
        case 'api':
            Object.assign(settings, apiConfig);
            break;
    }
    if (key === null) {
        return settings;
    }
    let keyArray = key.split(".");
    for (let i in keyArray) {
        if (keyArray.hasOwnProperty(i)) {
            if (settings.hasOwnProperty(keyArray[i])) {
                settings = settings[keyArray[i]];
            } else {
                settings = null;
                break;
            }
        } else {
            settings = null;
        }
    }
    return settings;
};
Vue.prototype.$setConfig = function(key,val){
    if(key){
        let newConfig = {
            systemConfig:{}
        };
        newConfig.systemConfig[key] = val;
        this.$store.commit('setServerConfig',newConfig);
    }
};
/**
 * 
 * @param val
 * @returns {string|boolean}
 */
Vue.prototype.$lang = function (val) {
    if (val) {
        localStorage.setItem('userCenterLang', val);
        return true;
    } else {
        let lang = localStorage.getItem('userCenterLang');
        if (!lang) {
            lang = this.$C('defaultLang', 'sys');
        }
        if (lang == 'lang_0') {
            this.$i18n.locale = 'zh';
        } else if (lang == 'lang_1'){
            this.$i18n.locale = 'tw';
        } else if (lang == 'lang_2') {
            this.$i18n.locale = 'en';
        }

        const langList = this.$C('langList', 'lang');
        if (!langList || !langList.hasOwnProperty(lang)) {
            lang = this.$C('defaultLang', 'sys');
        }
        return lang;
    }
};
Vue.prototype.$langID = function () {
    return this.$lang().replace('lang_', '');
};
/**
 * 
 * @param key {$1}
 * @param changeVal 
 * @param type 
 * @returns {string}
 */
Vue.prototype.$L = function (key, changeVal, type) {
    if (!key) {
        return '';
    }
    let langType = this.$lang();
    if (type) {
        langType = type;
    }
    let lang = key;
    if (langType.toString() !== 'lang_0' && langType.toString() !== 'lang_1') {
        const langList = this.$C('langList', 'lang');
        if (langList && langList.hasOwnProperty(langType)) {
            //
            let langArr = this.$C('langPackage.' + langType, 'lang');
            if (langArr && langArr.hasOwnProperty(key)) {
                lang = langArr[key];
            }
        }
    } else {
        //
        const changeTextShow = this.$C('changeTextShow');
        if (changeTextShow) {
            for (let k in changeTextShow) {
                lang = lang.replace(new RegExp(k, 'g'), changeTextShow[k]);
            }
        }
    }
    if (langType.toString() === 'lang_1') {
        //
        lang = spToFt(lang);
    }
    if (changeVal) {
        for (let i = 0; i < changeVal.length; i++) {
            lang = lang.replace('{$' + (i + 1) + '}', changeVal[i]);
        }
    }
    return lang;
};
/**
 *
 * @return {string}
 */
Vue.prototype.$browser = function () {
    let userAgent = navigator.userAgent; //userAgent
    let isOpera = userAgent.indexOf("Opera") > -1; //Opera
    let isIE = userAgent.indexOf("compatible") > -1
        && userAgent.indexOf("MSIE") > -1 && !isOpera; //IE
    let isEdge = userAgent.indexOf("Edge") > -1; //IEEdge
    let isFF = userAgent.indexOf("Firefox") > -1; //Firefox
    let isSafari = userAgent.indexOf("Safari") > -1
        && userAgent.indexOf("Chrome") == -1; //Safari
    let isChrome = userAgent.indexOf("Chrome") > -1
        && userAgent.indexOf("Safari") > -1; //Chrome

    if (isIE) {
        let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
        reIE.test(userAgent);
        let fIEVersion = parseFloat(RegExp["$1"]);
        if (fIEVersion == 7) {
            return "IE7";
        } else if (fIEVersion == 8) {
            return "IE8";
        } else if (fIEVersion == 9) {
            return "IE9";
        } else if (fIEVersion == 10) {
            return "IE10";
        } else if (fIEVersion == 11) {
            return "IE11";
        } else {
            return "0";
        }//IE
        return "IE";
    }
    if (isOpera) {
        return "Opera";
    }
    if (isEdge) {
        return "Edge";
    }
    if (isFF) {
        return "FF";
    }
    if (isSafari) {
        return "Safari";
    }
    if (isChrome) {
        return "Chrome";
    }
};
/**
 * 
 * @param obj
 * @return {boolean}
 */
Vue.prototype.$isEmpty = function (obj) {
    for (let i in obj) { // true
        return false;
    }
    return true;
};
/**
 * 
 */
Vue.prototype.$isMobile = function () {
    return isMobile();
};

Vue.prototype.$assignObj = function (a, b) {
    return assignObj(a, b);
};
/**
 * 
 * @param stringToSearch
 * @param arrayToSearch
 * @return {boolean}
 */
Vue.prototype.$in_array = function (stringToSearch, arrayToSearch) {
    for (let s = 0; s < arrayToSearch.length; s++) {
        let thisEntry = arrayToSearch[s];
        if (thisEntry === stringToSearch) {
            return true;
        }
    }
    return false;
};
/**
 * tokennulltokentoken
 * @type {token}
 */
Vue.prototype.$accessToken = function (token) {
    return accessToken(token);
};
/**
 * objpostformData
 * @param obj
 * @return {*}
 */
Vue.prototype.$objToFormData = function (obj) {
    if (typeof obj === 'object') {
        let param = new FormData();
        for (let key in obj) {
            let item = obj[key];
            if (item && typeof item === "object") {
                if (item.hasOwnProperty('file')) {
                    param.append(key, item.file, item.file.name);
                } else if (item.hasOwnProperty('fileList')) {
                    param = this.$paramAddFileArray(param, key, item.fileList);
                } else {
                    param = this.$paramAddObj(param, key, item);
                }
            } else {
                param.append(key, item);
            }
        }
        return param;
    } else {
        return obj;
    }
};
/**
 * formData
 * @param param
 * @param fatherKey
 * @param fatherVal
 * @return {*}
 */
Vue.prototype.$paramAddFileArray = function (param, fatherKey, fatherVal) {
    for (let key in fatherVal) {
        let item = fatherVal[key];
        if (item.hasOwnProperty('file')) {
            param.append(fatherKey + '[' + key + ']', item.file, item.file.name);
        } else if (item.hasOwnProperty('fileList')) {
            param = this.$paramAddFileArray(param, fatherKey + '[' + key + ']', item.fileList);
        }
    }
    return param;
};
/**
 * objformData
 * @param param
 * @param fatherKey
 * @param fatherVal
 * @return {*}
 */
Vue.prototype.$paramAddObj = function (param, fatherKey, fatherVal) {
    for (let key in fatherVal) {
        let item = fatherVal[key];
        if (typeof item === "object") {
            param = this.$paramAddObj(param, fatherKey + '[' + key + ']', item);
        } else {
            param.append(fatherKey + '[' + key + ']', item);
        }
    }
    return param;
};
/**
 * 
 * @return {boolean}
 */
Vue.prototype.$isDev = function () {
    if (process.env.NODE_ENV === "development") {
        return true;
    } else {
        return false;
    }
};
/**
 * 
 * @param errorType
 */
Vue.prototype.$goServerLogin = function (errorType = 2) {
    //let thisUrl = this.$route.fullPath;
    let url = '/login.php?err=' + errorType + '&redirect=' + encodeURIComponent(window.location.href);
    if (this.$isDev()) {
        url = this.$C('serverUrl', 'sys') + url;
    }
    window.location.href = url;
};
/**
 * 
 * @return {*}
 */
Vue.prototype.$themeColor = function () {
    let UserCenterThemeColor = this.$C('themeColor');
    if (UserCenterThemeColor) {
        //#(%23)
        UserCenterThemeColor = UserCenterThemeColor.replace('#', '');
        //
        UserCenterThemeColor = UserCenterThemeColor.toLowerCase();
    } else {
        UserCenterThemeColor = this.$C('themeColorDefault')
    }
    return UserCenterThemeColor;
};
/**
 * 
 * @type {lightenDarkenColor}
 */
Vue.prototype.$lightenDarkenColor = function (col, amt) {
    return lightenDarkenColor(col, amt);
};

Vue.prototype.$getWebId = function(){
    let webId=this.$C('web_id');
    if(!webId){
        let token = this.$accessToken();
        if(token){
            webId = token.slice(0, token.indexOf('.'));
        }
    }
    webId = parseInt(webId);
    if(isNaN(webId)){
        webId = 0;
    }
    return webId;
};
/**
 * 
 * @return {string}
 */
Vue.prototype.$homeUrl = function () {
    let webId=this.$getWebId();
    let homeUrl = this.$serverUrl();
    if (this.$isPublish()===0) {
        //
        homeUrl = '/sysTools.php?mod=diy&act=preview&idweb='+webId;
    }
    if (this.$isDev()) {
        //
        homeUrl = this.$C('serverUrl') + '/sysTools.php?mod=diy&act=preview&idweb='+webId;
    }
    return homeUrl;
};

Vue.prototype.$gotoWebHome = function () {
    window.top.location.href = this.$homeUrl();
};

Vue.prototype.$getLocationUrl = function () {
    return window.location.protocol + '//' + window.location.hostname + (window.location.port && window.location.port !== '80' && window.location.port !== '443' ? ":" + window.location.port : '');
};
/**
 * 
 * @param a
 * @param b
 * @return {boolean}
 */
Vue.prototype.$compareObj = function (a, b) {
    if (a === b) {
        return true;
    }
    for (let key in a) {
        //
        if (!a[key] || !b.hasOwnProperty(key) || a[key] != b[key]) {
            return false;
        }
    }
    return true;
};
/**
 * false
 * @return {{langConfig: {langPackage, langList}, systemConfig}|boolean}
 */
Vue.prototype.$isPublish = function (type) {
    return isPublish(type);
};

Vue.prototype.$serverUrl = function (val) {
    return serverUrl(val);
};

Vue.prototype.$getBase64 = function(src, cb) {
    let _self = this;
    let image = new Image();
    if(src && src.indexOf('http')===0){
        //url
        _self.$getApi('getImgBase64',{url:src},(reData)=>{
            //success
            image.src = reData.img;
            image.crossOrigin = _self.$getLocationUrl();  // 
            image.onload = function(){
                try {
                    let base64 = _self.$getBase64Image(image);
                    if(typeof cb === "function"){
                        cb(base64);
                    }
                }catch (e) {
                    this.$message.error(e.toString());
                    cb(src);
                }
            };
        },(error)=>{
            //error
            this.$message.error(error.msg);
            cb(src);
        });
    }else{
        if(typeof cb === "function"){
            cb(src);
        }
    }
};

Vue.prototype.$getBase64Image= function(img) {
    let canvas = document.createElement("canvas");
    canvas.width = img.width;
    canvas.height = img.height;
    let ctx = canvas.getContext("2d");
    ctx.drawImage(img, 0, 0, img.width, img.height);
    let dataURL = canvas.toDataURL("image/png");  //  image/jpeg
    return dataURL;
};

Vue.prototype.$clone = function(obj){
    return clone(obj);
};

/**
 * 
 */
Vue.prototype.$oem = function(val){
    return oem(val);
};
